html{
  scroll-behavior: smooth;
}

.bg-light{
  background: white;
}

body{
  font-family:'Montserrat';
}

.nav-item{
  margin: 5px;
}

.nav-link{
  color: #282c34;
}

.navbar-toggler{
  border : none;
  color: #282c34;
}

.logo{
  width: 100%;
  max-width: 50px;
  height: auto;
}

.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


